import React, { useEffect, useState } from 'react';
import "./Test_viewstyles.css"
export default function Test() {
    const [quizData, setQuizData] = useState([]);

  useEffect(() => {
    fetch('/app/view_test/')
      .then((response) => response.json())
      .then((data) => setQuizData(data));
  }, []);

  return (
    <div>
        <form method='post' action='/app/responses/'>
            {quizData.map((quiz) => (
                <div key={quiz.question_no}>
                <h2>Question {quiz.question_no}: {quiz.question}</h2>
                <ul>
                <label>
                    <p>Option 1 : {quiz.option1}</p>

                </label>
                <label>
                    <p>Option 2 : {quiz.option2}</p>

                </label>
                <label>
                    <p>Option 3 : {quiz.option3}</p>

                </label>
                <label>
                    <p>Option 4 : {quiz.option4}</p>

                </label>
                <label>
                    <p>Answer : {quiz.answer}</p>

                </label>
                <input type="hidden" value={quiz.quizid} name="quiz_id" />
                </ul>
                </div>
            ))}
            <div className="buttons">
                <button type="submit" id="res_button">View submissions</button>
            </div>
        </form>
    </div>
  );
}






