import React from 'react'
import "./NavbarStyle.css";
import { NavLink } from 'react-router-dom';
import { Outlet } from 'react-router-dom';
import companyLogo from "../images/logo.png"
const Navbar = () => {
  return (
    <>
      {/* <div className="container"> */}

      <div className="hero">
        <div className="logo">
          <NavLink to="/"> <div><img src={companyLogo} alt="Logo Here" className='logo1' /></div></NavLink>
        </div>
          {/* <div className="itemlist"> */}
          {/* <div> */}
          <ul className='List'>
            <NavLink to="/"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                textDecoration: 'none',
                fontWeight: '500',
                // background: isActive ? '#7600dc' : '#f0f0f0',
              })}
            ><li className='listItem'>HOME </li> </NavLink>

            <NavLink to="/app/about"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                // background: isActive ? '#7600dc' : '#f0f0f0',
                textDecoration: 'none',
                fontWeight: '500',
              })}
            > <li className='listItem'>ABOUT US</li></NavLink>

            <NavLink to="/app/features"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                // background: isActive ? '#7600dc' : '#f0f0f0',
                textDecoration: 'none',
                fontWeight: '500',
              })}
            > <li className='listItem'>FEATURES</li></NavLink>
            <NavLink to="/app/login_page"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                // background: isActive ? '#7600dc' : '#f0f0f0',
                textDecoration: 'none',
                fontWeight: '500',
              })}
            > <li className='listItem'>LOG IN</li></NavLink>

            {/* <a href="/form" target="_blank" rel="noreferrer"><li className='listItem login'>LOG IN</li></a> */}


          </ul>
          {/* </div> */}
        </div>
      <Outlet />
    </>
  )

}

export default Navbar
