import React from 'react'
import { useState } from "react";
import "./Questionstyles.css"
// import Input from "./Input";
const Questions = () => {
  const [formFields, setFormFields] = useState([
    { QuestionNumber: '', Question: '', option1: '', option2: '', option3: '', option4: '', answer:''},
  ])

  const handleFormChange = (event, index) => {
    let data = [...formFields];
    data[index][event.target.name] = event.target.value;
    setFormFields(data);
  }

  const submit = (e) => {
    e.preventDefault();
    console.log(formFields)
  }

  const addFields = () => {
    let object = {
      QuestionNumber: '',
      Question: '',
      option1: '',
      option2: '',
      option3: '',
      option4: '',
      answer: ''
    }
    setFormFields([...formFields, object])
  }

  const removeFields = (index) => {
    let data = [...formFields];
    data.splice(index, 1)
    setFormFields(data)
  }


  return (
    <>
    <div className="outerquestionsdiv">
      {/* <form onSubmit={submit}> */}
      <form method='post' action='/app/questions/'>
      <input className='quizid' name='quizid' type="number" placeholder='Quiz ID'/>
      <input className='quizid' id='qui' name='quiz_desc' type="text" placeholder='Quiz Description'/>
        {formFields.map((form, index) => {
          return (
            <div key={index} id='formdiv'>
              <input
              type='number'
                name='QuestionNumber'
                placeholder='Question Number'
                onChange={event => handleFormChange(event, index)}
                value={form.QuestionNumber}
              />
              <input
                name='Question'
                placeholder='Question'
                onChange={event => handleFormChange(event, index)}
                value={form.Question}
              />
              <div className="field">
                <label htmlFor="opt1"><pre>1.    </pre></label>
                <input
                id='opt1'
                  name='option1'
                  placeholder='Option 1'
                  onChange={event => handleFormChange(event, index)}
                  value={form.option1}
                />
              </div>
              <div className="field">
                <label htmlFor="opt2"><pre>2.    </pre></label>
                <input
                id='opt2'
                  name='option2'
                  placeholder='Option 2'
                  onChange={event => handleFormChange(event, index)}
                  value={form.option2}
                />
              </div>
              <div className="field">
                <label htmlFor="opt3"><pre>3.    </pre></label>
                <input
                id='opt3'
                  name='option3'
                  placeholder='Option 3'
                  onChange={event => handleFormChange(event, index)}
                  value={form.option3}
                />
              </div>
              <div className="field">
                <label htmlFor="opt4"><pre>4.    </pre></label>
                <input
                id='opt4'
                  name='option4'
                  placeholder='Option 4'
                  onChange={event => handleFormChange(event, index)}
                  value={form.option4}
                />
              </div>
              <input
              name='answer'
              type="number" 
              onChange={event => handleFormChange(event, index)} 
              placeholder='Answer  [1 - 4]'
              value={form.answer}
              />
              <button onClick={() => removeFields(index)} id='removebtn'>Remove</button>
            </div>
          )
        })}
        <div className="buttons">
            <p id='add' onClick={addFields}>Add More..</p>
      <br />
      </div>
      <div className="buttons">
        <button type='submit' className="buttons">Submit</button>
      </div>
      </form>
      
    </div>
    </>
  )
}

export default Questions
