import React, { useState } from 'react';
import "./NavbarStyle.css";
import { NavLink } from 'react-router-dom';
import { Outlet, useNavigate} from 'react-router-dom';
import companyLogo from "../images/logo.png"
const Navbar = () => {
  const [data, setData] = useState(null);
  const history = useNavigate();

  async function handleButtonClick() {
    const response = await fetch('/app/logout/', {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    });
    const responseData = await response.json();
    setData(responseData);
    history.push('/');
  }
  return (
    <>
      {/* <div className="container"> */}
      <div className="hero">
        <div className="logo">
          <NavLink to="/"> <div><img src={companyLogo} alt="Logo Here" className='logo1' /></div></NavLink>
        </div>
          {/* <div className="itemlist"> */}
          {/* <div> */}
          <ul className='List'>
            <NavLink to="/app/landingpage1"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                textDecoration: 'none',
                fontWeight: '500',
                // background: isActive ? '#7600dc' : '#f0f0f0',
              })}
            ><li className='listItem'>CREATE QUIZ</li> </NavLink>

            <NavLink to="/app/rules"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                // background: isActive ? '#7600dc' : '#f0f0f0',
                textDecoration: 'none',
                fontWeight: '500',
              })}
            > <li className='listItem'>RULES</li></NavLink>
            <NavLink  onClick={handleButtonClick} to="/"
              style={({ isActive }) => ({
                color: isActive ? 'rgb(187, 132, 232)' : '#545e6f',
                // background: isActive ? '#7600dc' : '#f0f0f0',
                textDecoration: 'none',
                fontWeight: '500',
              })}
            > <li className='listItem'>LOG OUT</li></NavLink>

            {/* <a href="/form" target="_blank" rel="noreferrer"><li className='listItem login'>LOG IN</li></a> */}


          </ul>
          {/* </div> */}
        </div>
      <Outlet />
    </>
  )

}

export default Navbar
