import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Questions from './Questions';
import "./Landingpage2styles.css"

const Landingpage1 = () => {
  const [myObject, setMyObject] = useState(null);
  var n=0;
  const forms = [];

  useEffect(() => {
    fetch('/app/exam_que/')
    .then(response => response.json())
    .then(data => setMyObject(data)) 
    .catch(error => console.error(error));
  }, []);
  
  if (myObject && myObject.length) {
    n = Object.keys(myObject).length;
    
    for (let i = 0; i < n; i++) {
      forms.push(
        <form method='post' action='/app/attempt_quiz/'>
          <p>Quiz ID: {myObject[i][0]}</p>
          <p>Quiz Description: {myObject[i][1]}</p>
          <input type="hidden" value={myObject[i][0]} name="quiz_id" />
          <button type="submit">Attempt</button>
        </form>
      );
    }
  } else {
    return <div class="heading123">No quizzes available</div>;
  }
  return (
    <div>
        <br/>
        {/* <br/>
        {myObject[0][0]}
        <br/>
        {myObject[0][1]} */}
        {/* <br/>
        <form action="post" method='/app/attempt_quiz/'>
          <p>{myObject[0][0]}</p>
          <p>{myObject[0][1]}</p>
          <input type="hidden" value={myObject[0][0]} name='quiz_id'/>
          <button type='submit'>Attempt</button>
        </form> */}
        <br/>
        <div class="examsec">
        {forms}
        </div>


    </div>
  )
}

export default Landingpage1
