import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

export default function Test() {
    const navigate = useNavigate();
    const [quizData, setQuizData] = useState([]);

  useEffect(() => {
    fetch('/app/test/')
      .then((response) => response.json())
      .then((data) => setQuizData(data));
  }, []);

  const [value, setValue] = useState(false);

  useEffect(() => {
    const interval = setInterval(() => {
      fetch('/app/value_checker/')
        .then(response => response.json())
        .then(data => {
          if (data.value === true) {
               navigate('/app/landingpage2/');
          }
        })
        .catch(error => console.error(error));
    }, 1000); // check every 5 seconds

    return () => clearInterval(interval);
  }, []);

  return (
    <div>
        <form method='post' action='/app/answers/'>
            {quizData.map((quiz) => (
                <div key={quiz.question_no}>
                <h2>Question {quiz.question_no}:</h2>
                <p>{quiz.question}</p>
                <ul>
                <label>
                    <input type="checkbox" name="option1" value={quiz.question_no} />
                    {quiz.option1}
                </label>
                <label>
                    <input type="checkbox" name="option2" value={quiz.question_no} />
                    {quiz.option2}
                </label>
                <label>
                    <input type="checkbox" name="option3" value={quiz.question_no} />
                    {quiz.option3}
                </label>
                <label>
                    <input type="checkbox" name="option4" value={quiz.question_no} />
                    {quiz.option4}
                </label>
                </ul>
                </div>
            ))}
            <button type="submit">Submit</button>
        </form>
    </div>
  );
}






