import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import "./Landingpage1styles.css"
import "./Response_viewstyles.css"
import learning from "../images/distance-learning.svg"
const Response_view = () => {
  const [myObject, setMyObject] = useState(null);
  var n=0;
  const forms = [];

  useEffect(() => {
    fetch('/app/my_quizzes/')
    .then(response => response.json())
    .then(data => setMyObject(data))
    .catch(error => console.error(error));
  }, []);

  if (myObject && myObject.length) {
    n = Object.keys(myObject).length;

    for (let i = 0; i < n; i++) {
      forms.push(
        <form className="response" method='post' action='/app/view_my_responses/'>
        <div className="viewquizdiv1">
          <p>Quiz ID : {myObject[i][0]}</p>
          <p>Marks : {myObject[i][1]}</p>
          <p>Quiz Status : {myObject[i][2]}</p>
          <input type="hidden" value={myObject[i][0]} name="quiz_id" />
          <button type="submit">View responses</button>
          </div>
        </form>
      );
    }
  } else {
    return <div class="heading123">No quizzes attempted</div>;
  }
  return (
    <>
    <div className="sec1" id='landingpage1div1'>
      <div class="examsec">
        {forms}
      </div>
    </div>
    </>
  )
}

export default Response_view