import React from 'react'
import "./ResultStyles.css"
const Result = () => {
  let examno=0, quizdescription=0, usermarks=0, totalmarks =0;
  return (
    <div>
        <div className="heading">Result Section</div>
        <div className="head"><b>Quiz No. </b>{ examno }</div>
        <div className="head"><b>Quiz Description:  </b>{ quizdescription }</div>
        <div className="head"><b>Your Result :   </b>{ usermarks } / { totalmarks } </div>

    </div>
  )
}

export default Result