import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import "./Landingpage1styles.css"
import learning from "../images/distance-learning.svg"
const Landingpage1 = () => {
  const [myObject, setMyObject] = useState(null);
  var n=0;
  const forms = [];

  useEffect(() => {
    fetch('/app/exam_que_t/')
    .then(response => response.json())
    .then(data => setMyObject(data))
    .catch(error => console.error(error));
  }, []);

  if (myObject && myObject.length) {
    n = Object.keys(myObject).length;

    for (let i = 0; i < n; i++) {
      forms.push(
        <form method='post' action='/app/view_quiz/' id='viewquiz'>
          <div className="viewquizdiv">
          <p>{myObject[i][0]}</p>
          <p>{myObject[i][1]}</p>
      
            
          <input type="hidden" value={myObject[i][0]} name="quiz_id" />
          <button type="submit">View</button>
          </div>
        </form>
      );
    }
  } else {
    return <div>Loading...</div>;
  }
  return (
    <>
    <div className="sec1" id='landingpage1div1'>
        <div className="leftsec" id='lp1left'>
          {/* <h2>ONLINE PROCTORING MADE BETTER</h2> */}
          {/* <div className="seewhy">See why this student prefers Examix over other online proctoring platforms</div> */}
          <div className="seewhyinfo" id='engagestudents'>"Engage your students and test their knowledge with our user-friendly quiz creation tool. With just a few clicks, you can create interactive multiple-choice quizzes that will help your students master the material and achieve their academic goals."</div>
          <div className="lp1btn">
          <NavLink to="/app/questions"><button id='createquiz'>CREATE QUIZ</button></NavLink>
          </div>
        </div>
        <div className="rightsec"  id='lp1right'>
          <img src={learning} alt="" id="img1landingpg1" />
        </div>
      </div>
      <div class="examsec">
        <div className="examdisplayhead">Created Quizzes</div>
        {forms}
      </div>
    </>
  )
}

export default Landingpage1