import React from 'react'
import "./FormStyles.css"
import { NavLink } from 'react-router-dom';
export default function Form() {
  return (
    <div className='formouter'>
      <form  action='/app/teacher_login/' method='post'>
        <h4>Are you a Teacher?</h4>

        <label for="username">UserID</label>
        <input type="number" placeholder="Enter your User ID" id="username1" name='username' required/>

        <label for="password">Password</label>
        <input type="password" placeholder="Enter your password" id="password1" name='password' required/>

        <div className="buttonsdiv">
        <button>Log In</button>
        {/* <NavLink to="/register"><button>Sign Up</button></NavLink> */}
        </div>
        {/* <div class="social">
          <div class="go"><i class="fab fa-google"></i> Google</div>
          <div class="fb"><i class="fab fa-facebook"></i> Facebook</div>
        </div> */}
      </form>
      <form action='/app/login/' method='post'>
      <h4>Are you a Student?</h4>

        <label for="username">UserID</label>
        <input type="number" placeholder="Enter your User ID" id="username" name='username' required/>

        <label for="password">Password</label>
        <input type="password" placeholder="Enter your password" id="password" name='password' required/>

        <div className="buttonsdiv">
        <button>Log In</button>
        <NavLink to="/app/register/"><button>Sign Up</button></NavLink>
        </div>
        {/* <div class="social">
          <div class="go"><i class="fab fa-google"></i> Google</div>
          <div class="fb"><i class="fab fa-facebook"></i> Facebook</div>
        </div> */}
      </form>
    </div>
  )
}
