import React from 'react'
import "./Rulesstyles.css"
const Rules = () => {
  return (
    <>
    <div id='heading2'>Rules</div>
    <div className="rulespoints">
      <ul>
        <li>Do not communicate with anyone during the examination. This includes talking, messaging, or using any other form of communication.</li>
        <li>Do not use any external resources or aids during the examination, such as books, notes, or calculators.</li>
        <li>Do not look away from the screen during the examination, and do not open any other applications or windows on your computer.</li>
        <li>Do not use any prohibited devices or technology during the examination, such as mobile phones or tablets.</li>
        <li>Make sure that your webcam and microphone are functioning properly before starting the examination.</li>
        <li>Do not leave the examination room or the camera's field of view during the examination</li>
        <li>Follow all instructions provided by the examination authority or software, including any specific guidelines for the examination.</li>
        <li>Do not attempt to cheat or engage in any other form of academic misconduct during the examination.</li>
        <li>If any technical issues arise during the examination, immediately send an email to the examination authority explaining the issue in detail. Be sure to include your name, exam code, and a clear description of the issue you are experiencing. This will help ensure that the issue is properly documented and can be addressed as soon as possible.</li>
        <li>Do not take any actions that could disrupt the examination or interfere with the monitoring software's ability to monitor it.</li>
      </ul>
    </div>
    </>
  )
}

export default Rules
