import React from 'react'
import "./FormStyles.css"
export default function Register() {
  return (
    <div className='formouter'>
      <form method='post' action='/app/register/'>
        <h4>Register Here</h4>
        <label for="name">Name</label>
        <input type="text" placeholder="Enter your name" id="name" name='name' required/>
        <label for="username">UserID</label>
        <input type="number" placeholder="Enter your username" id="username" name='username' required/>
        <label for="email">Email</label>
        <input type="email" placeholder="Email" id="email" name='email' required/>
        <label for="password">Password</label>
        <input type="password" placeholder="Enter your password" id="password" name='password' required/>
        <label for="branch">Branch</label>
        <input type="text" placeholder="Enter your branch" id="branch" name='branch' required/>
        <label for="phone">Phone no.</label>
        <input type="number" placeholder="Enter your contact number" id="phone" name='phone' pattern="[0-9]{10,}" maxlength="10" required/>
        <label for="year">Year</label>
        <input type="number" placeholder="Enter your current year of study 1-4" id="year" name='year'  pattern="[1-4]{1,}" required/>
        <div className="buttonsdiv">
        <button>Register</button>
        </div>
        {/* <div class="social">
          <div class="go"><i class="fab fa-google"></i> Google</div>
          <div class="fb"><i class="fab fa-facebook"></i> Facebook</div>
        </div> */}
      </form>
    </div>
  )
}
