import React, { useEffect, useState } from 'react';
import "./Test_viewstyles.css"
export default function Selected_responses() {
    const [quizData, setQuizData] = useState({ myList: [], score: '', quiz_status: '', images: []});

  useEffect(() => {
    fetch('/app/view_selected_responses_r/')
      .then((response) => response.json())
      .then((data) => setQuizData(data));
  }, []);

  return (
  <>
    <div>
        <form>
            <h2>Marks : {quizData.score}</h2>
            <h2>Quiz Status : {quizData.quiz_status}</h2>
            {quizData.myList.map((item, index) => (
                <li key={index}>
                    <label>
                        <p>Question No. : {item[0]}</p>
                    </label>
                    <label>
                        <p>Selected Option : {item[1]}</p>
                    </label>
                    <label>
                        <p>Actual Answer : {item[2]}</p>
                    </label>
                    <label>
                        <p>Status : {item[3]==1 ? "Correct" : "Wrong"}</p>
                    </label>
                </li>
            ))}
        </form>
            { quizData.images.length ? <h2 id='insight' class="heading123">Some doubtful insights of quiz</h2> : '' }

             <div className= 'img_div'>
              {quizData.images.map((image, index) => (
                <img className='copy_img' key={index} src={`data:image/jpeg;base64,${image}`} alt={`Image ${index}`} />
              ))}
        </div>
    </div>
    </>
  );
}